import * as ajaxUrl from '../../ajax.config.js';

export default {
  fullTime(data) {
    let date = new Date(data);
    let year = date.getUTCFullYear() + '-';
    let month = (date.getUTCMonth() + 1 < 10 ? '0' + (date.getUTCMonth() + 1) : date.getUTCMonth() + 1) + '-';
    let day = (date.getUTCDate() < 10 ? '0' + date.getUTCDate() : date.getUTCDate()) + ' ';
    return year + month + day
  },
  durationTime(begin,end){
    let duration = Math.ceil((begin - end) / 1000 / 60 / 60 / 24);
    return duration + '天'
  },
  toFixed(number) {
    let num = new Number(number);
    return num.toFixed(2);
  }
}



